.container{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: white;
    flex-wrap: wrap;
}
.container > div > h2{
    color: white;
    background-color: var(--color-purple);
    padding: .3rem;
    height: max-content;
    width: 100vw;
    border-top-left-radius: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    text-align: center;
    font-size: 1.4rem;
}
.thead{
    width: 95vw;
    height: 3vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: white;
    color: var(--color-purple);
}
.thead > p {
    width: 10vh;
    color: var(--color-purple);
    padding: 0;
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-size: 1rem;
}
.thead > div{
    width: 40%;
    height: 4vh;
}
.thead > :nth-child(4){
    width: 16vh;
    height: 4vh;
}

@media only screen and (max-width: 900px) and (orientation: landscape) {
   .thead{
       height: 2rem;
   }
}